<template>
	<div id="service-comment">
		<public-header title="用户评价"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == -1}" @click="onToggleStatus(-1)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">待审核</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">审核通过</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">审核未通过</div>
				</div>
				<van-search placeholder="请输入用户手机号或UID" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="comment-list"
			>
				<div class="comment-item" v-for="(item, index) in records.data" :key="item.id">
					<van-row>
						<van-col span="4">
							<div class="comment-img">
								<img class="img-100" :src="item.avatar" alt="">
							</div>
						</van-col>
						<van-col span="20">
							<div class="comment-right">
								<div class="comment-title flex-lr">
									<div class="flex">
										<div class="comment-number">{{item.mobile}}</div>
										<div class="c-danger font-bold">{{item.score}}星</div>
									</div>
									<div class="font-13 c-gray">{{item.addtime_cn}}</div>
								</div>
								<div class="padding-10-b flex-lr">
									<div class="flex">
										<div class="font-12 c-gray">商品质量</div>
										<van-rate
										v-model="item.goods_quality"
										:size="12"
										:count="5"
										color="#ffd21e"
										/>
									</div>
									<div class="flex">
										<div class="font-12 c-gray">配送服务</div>
										<van-rate
										v-model="item.delivery_service"
										:size="12"
										:count="5"
										color="#ffd21e"
										/>
									</div>
								</div>
								<div class="flex font-14 c-default padding-10-b">{{item.note}}</div>
								<div class="goods-img" v-if="item.thumbs">
									<div class="goods-img-detail" v-for="(thumbs, index) in item.thumbs" @click="util.jsPreviewImage(item.thumbs)">
										<img class="img-100" :src="thumbs" alt="">
									</div>
								</div>
								<div class="flex-lr padding-10-b">
									<div class="comment-address">
										<i class="icon icon-locationfill c-danger"></i>
										<span class="font-12 c-default">{{item.store.title}}</span>
									</div>
									<div class="font-12 c-gray" v-if="item.delivery">配送员：{{item.delivery.title}}</div>
								</div>
								<div class="replay margin-10-b" v-if="item.reply">
									 商家回复({{item.replytime_cn}}): {{item.reply}}
								</div>
								<div class="comment-check">
									<img v-if="item.status == 1" class="img-100" src="static/img/pass-check.png" alt="">
									<img v-else-if="item.status == 2" class="img-100" src="static/img/no-pass-check.png" alt="">
									<img v-else-if="item.status == 0" class="img-100" src="static/img/wait-check.png" alt="">
								</div>
							</div>
						</van-col>
					</van-row>
					<div class="border-1px-t btn-group">
						<div class="btn-item bg-danger" @click="onChangeStatus(item, 2, index)" v-if="item.status != 2">审核未通过</div>
						<div class="btn-item bg-primary" @click="onChangeStatus(item, 1, index)" v-if="item.status != 1">审核通过</div>
						<div class="btn-item bg-primary" @click="onToogleReplyPopup(item.id, index)">回复</div>
						<router-link tag="div" class="btn-item bg-info" :to="util.getUrl({path: '/pages/order/detail', query:{id: item.oid}})">查看订单</router-link>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>暂无评价!</p>
			</div>
			</van-pull-refresh>
		</div>
		<!--回复弹出层-->
		<van-popup v-model="replyPopup" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onToogleReplyPopup">取消</span>
				<span>评论回复</span>
				<span class="c-primary" @click="onConfirmReply">确定</span>
			</div>
			<div class="popup-content">
				<van-field
					v-model="reply"
					type="textarea"
					placeholder="限300字符，请勿恶意回复，一经查实将严肃处理，回复后不可修改"
					rows="8"
				/>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				showPreLoading: true,
				isRefresh: false,
				filter: {
					items: {
						status: '-1'
					}
				},
				replyPopup: false,
				reply: '',
				replyId: 0,
				replyIndex: 0,
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/service/comment/list'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onChangeStatus(item, status, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/service/comment/status',
					data: {
						id: item.id,
                        status: status
					},
					confirm: '确定修改审核状态吗？',
					success: function(){
						that.records.data[index].status = status;
					}
				});
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			},
			onToogleReplyPopup(replyId, replyIndex) {
				this.replyPopup = !this.replyPopup;
				if(replyId > 0 && replyIndex > -1) {
					this.replyId = replyId;
					this.replyIndex = replyIndex;
				}
			},
			onConfirmReply() {
				var that = this;
				if(!that.reply) {
					this.util.$toast('回复内容不能为空');
					return;
				}
				if(that.replyId > 0 && that.replyIndex > -1) {
					setData({
						vue: that,
						url: 'plateform/service/comment/reply',
						data: {
							id: that.replyId,
							reply: that.reply,
						},
						success: function(res) {
							that.records.data[that.replyIndex] = Object.assign(that.records.data[that.replyIndex], res)
							that.reply = '';
							that.util.$toast('回复成功');
							that.onToogleReplyPopup();
						}
					})
				}
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			}
		}
	}
</script>

<style>
	#service-comment .content .comment-item{
		background-color: #fff;
		margin-bottom: 10px;
	}
	#service-comment .content .comment-item .comment-img{
		width: 45px;
		height: 45px;
		padding-top: 10px;
		padding-left: 10px;
	}
	#service-comment .content .comment-item .comment-img img{
		border-radius: 100%;
	}
	#service-comment .content .comment-item .comment-right{
		position: relative;
		padding-top: 10px;
		padding-right: 10px;
	}
	#service-comment .content .comment-item .comment-right .comment-title{
		height: 35px;
	}
	#service-comment .content .comment-item .comment-right .comment-title .comment-number{
		font-size: 17px;
		font-weight: bold;
		padding-right: 10px;
	}
	#service-comment .content .comment-item .comment-right .goods-img{
		display:flex;
		flex-flow:row wrap;
		align-items:center;
		padding-bottom: 10px;
		margin-left: -5px;
	}
	#service-comment .content .comment-item .comment-right .goods-img .goods-img-detail{
		width: 25%;
		padding: 5px;
	}
	#service-comment .content .comment-item .comment-right .comment-address{
		max-width: 65%;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		font-size: 12px;
		color: #333;
	}
	#service-comment .content .comment-item .comment-right .replay{
		font-size:14px;
		color:#898989;
		padding:10px;
		background:#F4F4F4;
	}
	#service-comment .content .comment-item .comment-right .comment-check{
		position: absolute;
		top: 5px;
		right: 30px;
		width: 123px;
		height: 100px;
	}
	#service-comment .content .comment-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#service-comment .content .comment-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


